.scene-2-page-4 {
  .header {
    max-width: 80%;
  }

  &__spinner {
    position: relative;
    margin-top: 5.926vmin;
    background-image: url(../images/scene-2-page-4-spinner.svg);
    background-size: cover;
    @include calculateItemSizeVH(1104, 629);
    max-width: 100vw;
    margin-bottom: 2vh;
  }

  &__text {
    @include fontProperty(2.407vmin, 1.2, 400, #fff, left)
  }

  &__text1 {
    position: absolute;
    top: 11%;
    left: 6%;
  }

  &__text2 {
    position: absolute;
    top: 47%;
    right: 10%;
  }

  &__text3 {
    position: absolute;
    bottom: 11%;
    left: 6%;
  }
}

@media (max-aspect-ratio: 7/4) {
  .scene-2-page-4 {
    .header {
      max-width: 100%;
      font-size: 3.5vmin;
    }
  }
}
